import React from 'react';
import { HashRouter,BrowserRouter, Route, Switch } from 'react-router-dom';
import Home from './Home';
import Login from './Login';


const App = () => {

  return (
    <div className="wrapper">
 
      <HashRouter>
        <Switch>
        <Route path="/search">
            <Home />
          </Route>
          <Route path="/">
            <Login />
          </Route>
        </Switch>
      </HashRouter>
    </div>
   

  );
};

export default App;
